import $ from 'jquery';

export function isValidForm(formId) {
  let hasError = false;
  $('#'+formId+ ' input, #'+formId+ ' select').each(function() {
    if($(this).attr('mandatory') === 'true' &&
    ($(this).val() === undefined || $(this).val() === null || $(this).val().trim() === '')) {
      $(this).parent('.form-group').addClass('has-error');
      hasError = true;
    } else {
      $(this).parent('.form-group').removeClass('has-error');
    }
  });
  return !hasError;
}

export function highlightError(input) {
  let inputElement = $('#'+input);
  inputElement.parent('.form-group').addClass('has-error');
}



