import {BASE_URL} from './services'
export function saveFinanceDetails(params) {
  return fetch(BASE_URL + '/manufacturer/finance',{
    method:'post',
    body: JSON.stringify(params),
    headers: {'Content-Type':'application/json'}
  }).then(res => {
    return res;
  });
}

export function getFinanceDetails(orgId) {
  return fetch(BASE_URL + '/manufacturer/finance/'+orgId).then(response => {
    return response.json();
  });
}

export function getTechnicalDetails(orgId) {
  return fetch(BASE_URL + '/manufacturer/technical/'+orgId).then(response => {
    return response.json();
  });
}

export function saveTechnicalDetails(params) {
  return fetch(BASE_URL + '/manufacturer/technical',{
    method:'post',
    body: JSON.stringify(params),
    headers: {'Content-Type':'application/json'}
  }).then(res => {
    return res;
  });
}

export function getGeneralDetails(orgId) {
  return fetch(BASE_URL + '/manufacturer/general/'+orgId).then(response => {
    return response.json();
  });
}



