import React from 'react';
import AppComponent from '../AppComponent';
import FormElement from '../FormElement';
import {Row, Button, FormGroup, ControlLabel, FormControl, Checkbox, Radio} from 'react-bootstrap';

class SupplierDetails extends AppComponent {
  render() {
    return(
      <div>
        <Row className="col-md-12" style={{'marginBottom':'20px'}}>
        <FormGroup className="col-md-6" controlId="typeofjob">
          <ControlLabel>Type of Jobs</ControlLabel>
          <FormControl componentClass="select" multiple>
            <option value="Press Components">Press components</option>
            <option value="Fabricated Components">Fabricated components</option>
            <option value="Precision Machined Components">Precision Machined components</option>
            <option value="Plastic Moulding Components">Plastic Moulding components</option>
            <option value="Rubber Components">Rubber components</option>
            <option value="Foundry">Foundry</option>
          </FormControl>
        </FormGroup>
        </Row>
        <Row className="col-md-12">
          <FormGroup className="col-md-12">
            <ControlLabel>Please select the type of industries to which you would provide outsourcing jobs:</ControlLabel>
            <Row>
            <Checkbox className="col-md-3" name="microindustries" id="microindustries">
              Micro Industries
            </Checkbox>
            <Checkbox className="col-md-3" style={{'marginTop':'10px'}} name="smallindustries" id="smallindustries">
              Small Industries
            </Checkbox>
            <Checkbox className="col-md-3" style={{'marginTop':'10px'}} name="mediumindustries" id="mediumindustries">
              Medium Industries
            </Checkbox>
            <Checkbox className="col-md-3" style={{'marginTop':'10px'}} name="largeindustries" id="largeindustries">
              Large Industries
            </Checkbox>
            </Row>
          </FormGroup>
        </Row>
        <Row className="col-md-12">
          <FormGroup className="col-md-12">
            <ControlLabel>Please select the type of industries to which you would provide outsourcing jobs:</ControlLabel>
            <Row className="col-md-12">
            <Radio name="radioGroup" inline>
              Yes
            </Radio>{' '}
            <Radio name="radioGroup" inline>
              No
            </Radio>{' '}
            <Radio name="radioGroup" inline>
              Not Mandatory
            </Radio>
            </Row>
          </FormGroup>
        </Row>
      </div>
    );
  }
}

export default SupplierDetails;



